import React, { useState } from "react";
import { Button, Form } from "react-bootstrap";
import { useNavigate } from "react-router-dom";
import api from "../../api";
import { toast } from "react-toastify";
import ClientMethods from "../../asyncMethods/ItrMethods";

const AnnualFillingButton = ({ clientDetails, onStatusUpdate }) => {
  const [selectedFile, setSelectedFile] = useState(null);
  const [resp, setResp] = useState(null);
  const clientMethods = ClientMethods();
  const navigate = useNavigate();
  const jwt = localStorage.getItem("jwt");
  const headers = {
    headers: {
      Authorization: `Bearer ${jwt}`
    }
  };

  // const redirectToEditPage = (salaryDetailId) => {
  //   navigate("/itrrequest/edit/" + clientDetails.salaryDetailId);
  // };
  const uploadFile = (data) => {
    clientMethods.checkSessionExpiryOnDashboard();

    const id = clientDetails.annualFillingRequestId;

    let data2; 

    return api
      .post(`/user/uploadUserDocs/${id}`, data, headers)
      .then((res) => {
        data2 =  res.data.result;
        console.log("reee", res.data.result);
      }).then((ress) => {
        api.patch(`user/updateannualfilingurl/${id}`, {
          "annualFillingUrl": `${data2}`
        } , headers)
        .then((res) => {
          toast.success("successfully updated");
        });  
      })
      .catch((err) => {
        console.log(err);
        toast.error("Error uploading");
      });
  };

  const onFileSelect = async (e) => {
    let formData = new FormData();
    setSelectedFile(e.target.files[0]);
    const id = clientDetails.id;

    formData.append("file", e.target.files[0]);
    formData.append("fileKeyName", `annual-filling-pdf-upload`);

    const response = await uploadFile(formData);
    console.log("pro resp", response);

    if (response) {
      api
        .patch(
          `/user/updatecompanycomplience/${id}`,
          {
            annualFillingUrl: response
          },
          headers
        )
        .then((res) => {
          toast.success("Upload sucsessfull");
        })
        .catch((err) => {
          console.log(err);
          toast.error("Error uploading");
          setSelectedFile(null);
        });
    }
  };

  const handleRemoveFile = () => {
    setSelectedFile(null);
  };

  return (
    <>
      <div className="d-flex align-items-center">
        {/* <Button
            variant="primary"
            size="sm"
            // onClick={() => redirectToEditPage(clientDetails.salaryDetailId)}
          >
            <i className="fa-solid fa-user-pen"></i>
            <input type='file'/>
          </Button>{" "} */}
        <div className="upload-btn-wrapper">
          <label> </label>
          {!selectedFile && (
            <>
              <button className="btn-fill btn btn-info pointer">Upload</button>
              <input
                type="file"
                name="myfile"
                accept=".pdf"
                onChange={(e) => onFileSelect(e)}
              />{" "}
            </>
          )}
          {selectedFile && (
            <div className="selected-file-info">
              <span>{selectedFile.name}</span>
              <button
                className="btn-fill btn btn-info pointer"
                onClick={handleRemoveFile}
              >
                Remove
              </button>
            </div>
          )}
        </div>
        {/* <Form.Check
            inline
            className={"mx-3 text-danger"}
            type="switch"
            value={clientDetails.clientActiveStatus === 1 ? 1 : 0}
            checked={clientDetails.clientActiveStatus === 1 ? true : false}
            id={"toggle-button" + clientDetails.salaryDetailId}
            onChange={(e) => {
              onStatusUpdate(clientDetails.salaryDetailId, e.target.value);
            }}
          /> */}
      </div>
    </>
  );
};

export default AnnualFillingButton;


