import { useDispatch } from "react-redux";
import { useNavigate, useParams } from "react-router-dom";
import { toast } from "react-toastify";
import {
  FETCH_ALL_CLIENT,
  SET_EDIT_CLIENT
} from "../redux/constants/ClientActionTypes";
import api from "../api";
import { clientLogin } from "../redux/actions/ClientAction";
import axios from "axios";
// import { FETCH_ALL_VEHICLE } from "../redux/constants/VehicleActionTypes";

const ClientMethods = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const parameters = useParams();
  const jwt = localStorage.getItem("jwt");
  // const user = JSON.parse(localStorage.getItem("user"));
  // const sub = JSON.parse(localStorage.getItem("sub"));
  const id = parameters.irtId;
  const headers = {
    headers: {
      Authorization: `Bearer ${jwt}`
    }
  };

  function checkSessionExpiryOnDashboard() {
    const loginTime = localStorage.getItem("loginTime");

    // Check if login time is available in local storage
    if (loginTime) {
      const currentTime = new Date().getTime();
      const timePassed = currentTime - parseInt(loginTime, 10);
      const expiryTime = 600000; // 60,000 milliseconds (60 seconds)

      // Check if the specified duration has passed
      if (timePassed > expiryTime) {
        // Clear localStorage and perform logout actions
        toast.error("Jwt expired");
        localStorage.clear();
        window.location.reload();
        navigate("/");
      }
    }
  }

  const login = data => {
    const payload = {
      // clientMobileNo: data.clientMobileNo,
      clientPassword: data.clientPassword
      // roleType: data.roleType,
    };

    if (/^\d{10}$/.test(data.username)) {
      payload.clientMobileNo = data.username;
    } else {
      payload.clientEmailId = data.username;
    }

    return api
      .post("/masterservice/clientLoginUser", payload)
      .then(res => {
        const result = res.data.result;
        if (Number(res.data.statusCode) === 401) {
          toast.error(res.data.statusDescription);
          localStorage.setItem("cliLog", true);
          return;
          // navigate("/clientLogin");
        } else {
          localStorage.setItem("user", JSON.stringify(res.data.result));
          const user = JSON.parse(localStorage.getItem("user"));
          if (user) {
            user.client = true;
            localStorage.setItem("user", JSON.stringify(user));
            console.log("Updated user object:", user);
          }

          dispatch(clientLogin(result));
          navigate("/dashboard");
          fetchAllClients();
          toast.success("Login Successful.");
        }
      })
      .catch(() => {
        toast.error("Login Failed.");
      });
  };

  const ClientLogout = () => {
    localStorage.clear();
    toast.success("Logout Successful.");
    navigate("/");
  };

  const fetchAllClients = () => {
    checkSessionExpiryOnDashboard();
    return api
      .get(
        "/opsservice/itrreturn/getallitr?page=0&size=20000&sortBy=salaryDetailId",
        headers
      )
      .then(res => {
        dispatch({
          type: FETCH_ALL_CLIENT,
          payload: res.data.result
        });
      })
      .catch(err => {
        console.log(err);
      });
  };

  const fetchAllClients2 = () => {
    checkSessionExpiryOnDashboard();
    return api
      .get("/user/getallcorpsubscritionpayment", headers)
      .then(res => {
        dispatch({
          type: FETCH_ALL_CLIENT,
          payload: res.data.result
        });
      })
      .catch(err => {
        console.log(err);
      });
  };

  const fetchAllClientsWithoutC = () => {
    checkSessionExpiryOnDashboard();
    return axios
      .get(
        "http://3.109.226.210:1111/opsservice/itrreturn/getallitr?page=0&size=20000&sortBy=salaryDetailId",
        headers
      )
      .then(res => {
        console.log("without", res.data);
      })
      .catch(err => {
        console.log(err);
      });
  };

  const storeIndiClient = data => {
    return api
      .post("/opsservice/itrreturn/saveindividualitrsalary", data, headers)
      .then(res => {
        if (res.data.statusCode == 201) {
          toast.success("itr created Successful.");
          navigate("/itr");
          return;
        }
        toast.error(res.data.statusDescription);
        console.log(res.data.result);
      })
      .catch(err => console.log(err));
  };

  const storeCorpClient = data => {
    return api
      .post("/opsservice/itrreturn/savecorporateitrsalary", data, headers)
      .then(res => {
        if (res.data.statusCode == 201) {
          toast.success("itr created Successful.");
          navigate("/itr");
          return;
        }
        toast.error(res.data.statusDescription);
        console.log(res.data.result);
      })
      .catch(err => console.log(err));
  };

  const fetchClient = (clientId = 1) => {
    return api
      .get(`/masterservice/getclientdata?clientId=` + clientId)
      .then(res => {
        dispatch({
          type: SET_EDIT_CLIENT,
          payload: res.data.result
        });
      })
      .catch(err => {
        console.log(err);
      });
  };

  const updateIndiClient = (userId, client) => {
    const jwt = localStorage.getItem("jwt");
    const id = parameters.irtId;

    const headers2 = {
      "Content-Type": "application/json",
      Authorization: `Bearer ${jwt}`
    };
    return api
      .patch(
        `/opsservice/itrreturn/updateindividualitrsalary/` + userId,
        client,
        headers
      )
      .then(res => {
        if (res.data.statusCode == 200) {
          toast.success("itr updated Successful.");
          navigate("/itr");
          return;
        }
        toast.error(res.data.statusDescription);
      })
      .catch(err => {
        console.log(err);
        toast.error("Edit Failed.");
      });
  };

  const updateCorpClient = (userId, client) => {
    const jwt = localStorage.getItem("jwt");
    const id = parameters.irtId;

    const headers2 = {
      "Content-Type": "application/json",
      Authorization: `Bearer ${jwt}`
    };
    return api
      .patch(
        `/opsservice/itrreturn/updatecorporateitrsalary/` + userId,
        client,
        {
          headers2
        }
      )
      .then(res => {
        if (res.data.statusCode == 200) {
          toast.success("itr updated Successful.");
          navigate("/itr");
          return;
        }
        toast.error(res.data.statusDescription);
      })
      .catch(err => {
        console.log(err);
        toast.error("Fetch Failed.");
      });
  };

  const clientStatusUpdate = ({ id, status }) => {
    const data = {
      clientActiveStatus: status
    };
    const headers = {
      "Content-Type": "application/json"
    };
    return api
      .put(`/masterservice/enabledisableclient?clientId=` + id, data, {
        headers
      })
      .then(res => {
        toast.success("Status Updated Successful.");
        fetchAllClients();
      })
      .catch(err => {
        console.log(err);
      });
  };

  // const fetchClientOptions = (datatype = "gsttype") => {
  //   return api
  //     .get(
  //       "/masterservice/masterdatalist?datatype=" +
  //         datatype +
  //         "&parentId=0&activeStatus=1"
  //     )
  //     .then(res => {
  //       console.log("res:", res);
  //       dispatch({
  //         type: SET_GST_REGISTRATION_OPTIONS,
  //         payload: res.data.result
  //       });
  //     })
  //     .catch(err => {
  //       console.log(err);
  //     });
  // };

  const uploadFile = data => {
    checkSessionExpiryOnDashboard();
    const jwt = localStorage.getItem("jwt");
    // const user = JSON.parse(localStorage.getItem("user"));
    // const sub = JSON.parse(localStorage.getItem("sub"));
    const id = parameters.irtId;
    const headers = {
      headers: {
        Authorization: `Bearer ${jwt}`
      }
    };
    return api
      .post(`/user/uploadUserDocs/${id}`, data, headers)
      .then(res => {
        return res.data.result;
      })
      .catch(err => {
        console.log(err);
        toast.error("Error uploading");
      });
  };

  return {
    login,
    ClientLogout,
    fetchAllClients,
    storeIndiClient,
    storeCorpClient,
    fetchClient,
    updateIndiClient,
    updateCorpClient,
    clientStatusUpdate,
    // fetchClientOptions,
    fetchAllClientsWithoutC,
    checkSessionExpiryOnDashboard,
    fetchAllClients2,
    uploadFile
  };
};

export default ClientMethods;
