import React, { useEffect, useMemo, useState } from "react";
import { Button, Card } from "react-bootstrap";
import DataTable from "react-data-table-component";
import { useNavigate } from "react-router-dom";
import { useSelector } from "react-redux";
import CVMethods from "../../../asyncMethods/CVMethods";
import FilterComponent from "../../../components/FilterComponent/Index";
import api from "../../../api";
import ItCompareButton from "../../../components/ActionButtons/ItCompareButton";
import UserMethods from "../../../asyncMethods/UserMethods";

const ITCompare = () => {
  const navigate = useNavigate();
  const cVMethods = CVMethods();
  const userMethods = UserMethods();
  const [datas, setDatas] = useState([]);
  const [users, setUsers] = useState([]);

  const columns = [
    { name: 'id', selector: (row) => row.id},
    { name: "user ", selector: (row) => row.userId },
    {
      name: "Financial Year",
      selector: (row) => row.financialYear
    },
    {
      name: "Income ",
      selector: (row) => row.income
    },
    {
      name: "Investment ",
      selector: (row) => row.investment
    },
    {
      name: "Savings ",
      selector: (row) => row.saving
    },
    {
      name: "Actions",
      selector: (row) => row.actionButtons,
      center: true
    }
  ];

  const user = JSON.parse(localStorage.getItem("user"));
  // const userId = user?.userID;
  // const role = user?.roleType;

  useEffect(() => {
    //  cVMethods.fetchAllFys();
    const jwt = localStorage.getItem("jwt");

    const headers = {
      headers: {
        Authorization: `Bearer ${jwt}`
      }
    };

    const fetchAllUsers = () => {
      userMethods.checkSessionExpiryOnDashboard();
      return api
        .get("/user/getallincomecomp", headers)
        .then((res) => {
          setDatas(res.data.result);
          console.log("ussss", res.data.result);
        })
        .catch((err) => {
          console.log(err);
        });
    };

    fetchAllUsers();

    // localStorage.removeItem("pageRefreshed");
  }, []);

  const statusUpdate = (clientId, status) => {
    const data = {
      id: clientId,
      status: status == 1 ? 0 : 1
    };
    cVMethods.clientStatusUpdate(data);
  };

  let data = [];

  const all = useSelector((state) => state);

  let userList = useSelector((state) => state.authUser) || [];
  if (userList) {
    userList = userList.userList;
  }

  const inspectionList = useSelector(
    (state) => state.cvinspections.CVinspectionList
  );
  console.log("all", all);
  console.log("inspectionList", inspectionList);
  console.log("users", users);

  if (datas) {
    data = datas.map((inspection) => ({
      ...inspection,
      actionButtons: (
        <ItCompareButton onStatusUpdate={statusUpdate} userDetails={inspection} />
      )
    }));
  }

  // console.log("userId", userId);
  // console.log("rolee", role);

  // if (role === "2" || role === 2) {
  //   data = data.filter((client) => client.userId == userId);
  //   console.log("datasss", data);
  // }

  const [filterText, setFilterText] = useState("");
  const [filterText2, setFilterText2] = useState("");
  const [resetPaginationToggle, setResetPaginationToggle] = useState(false);

  let filteredItems;
  const filteredItems2 = data.filter((item) => item.userId == filterText);

  console.log("fillltextt", filterText, filterText2);
  console.log("fillll222", filteredItems2);

  // if((filteredItems2 == null || filteredItems2 == undefined || filteredItems2.length == 0) && (filterText2 != null || filterText2 != "")) {
  // //   filteredItems = data.filter((item) =>
  // //   // item.userId.toString().toLowerCase().includes(filterText.toLowerCase())
  // //   item.bankName.toLowerCase().includes(filterText.toLowerCase()) ||
  // //   item.ifscCode.toLowerCase().includes(filterText.toLowerCase())
  // // );
  // filteredItems = data;
  // }
  // else{
  //   filteredItems = filteredItems2;
  // }
  filteredItems = data;

  // const filteredItems = data;

  const subHeaderComponentMemo = useMemo(() => {
    const handleClear = () => {
      if (filterText) {
        setResetPaginationToggle(!resetPaginationToggle);
        setFilterText("");
        setFilterText2("");
      }
    };

    return (
      <div className="d-flex">
        {/* <select
        style={{ border: "1px solid rgb(229, 229, 229)", borderRadius: "5px 0px 0px 5px", color: "#7b8188" }}
        className='mr-2'
        value={filterText}  // Set the value of the select to the current filterText
        onChange={(e) => {setFilterText(e.target.value);
          setFilterText2(e.target.value);
        }}  // Update filterText on select change
      >
        <option value={""}>search user</option>
        {users && users.map((item) => (
          <option key={item.id} value={item.id}>
            {item.name}
          </option>
        ))}
      </select> */}

        <FilterComponent
          onFilter={(e) => setFilterText(e.target.value)}
          onClear={handleClear}
          filterText={filterText}
        />
      </div>
    );
  }, [filterText, resetPaginationToggle, users]);

  return (
    <>
      <div className="d-flex justify-content-between m-3">
        <h5></h5>
        <Button
          onClick={() => navigate("/itcompare/create")}
          variant="primary"
        >
          <i className="fa-solid fa-plus"></i> New Income Compare
        </Button>
      </div>
      <Card>
        <Card.Body>
          <DataTable
            title="Income Comparison"
            columns={columns}
            data={filteredItems}
            pagination
            paginationResetDefaultPage={resetPaginationToggle}
            subHeader
            subHeaderComponent={subHeaderComponentMemo}
            persistTableHead
          />
        </Card.Body>
      </Card>
    </>
  );
};

export default ITCompare;
