const Fys = () => {
  const navigate = useNavigate();
  const cVMethods = CVMethods();
  const [datas, setDatas] = useState([]);

  const jwt = localStorage.getItem("jwt");
  const headers = {
    headers: {
      Authorization: `Bearer ${jwt}`
    }
  };

    const columns = [
      {
        name: "financial Year Id",
        selector: (row) => row.financialYearId,
        sortable: true,
      },
      {
        name: "financial Year",
        selector: (row) => row.financialYear,
      },
      // {
      //   name: "status",
      //   selector: (row) => row?.activeStatus == true ? "true" : "flase",
      //   center: true,
      // },
      {
        name: "Action",
        selector: (row) => row?.actionButtons,
        center: true,
      },
    ];

  const user = JSON.parse(localStorage.getItem("user"));
  // const userId = user?.userID;
  // const role = user?.roleType;

  useEffect(() => {
  //  cVMethods.fetchAllFys();

    const fetchAllFys = () => {
      return api
        .get("/opsservice/master/getallfinancialyears", headers)
        .then((res) => {
          setDatas(res.data.result);
          console.log("datas", res.data.result);
        })
        .catch((err) => {
          console.log(err);
        });
    };

    fetchAllFys();
    // localStorage.removeItem("pageRefreshed");
  }, []);

  const statusUpdate = (clientId, status) => {
    const data = {
      id: clientId,
      status: status == 1 ? 0 : 1,
    };
    cVMethods.clientStatusUpdate(data);
  };

    let data = [];

  const all = useSelector(
    (state) => state
  );

const inspectionList = useSelector(
  (state) => state.cvinspections.CVinspectionList
);
  console.log("all", all);
  console.log("inspectionList", inspectionList);

   if (datas) {
     data = datas.map((inspection) => ({
       ...inspection,
       actionButtons: (
         <CVInspectionActionButton
           onStatusUpdate={statusUpdate}
           clientDetails={inspection}
         />
       ),
     }));
   }

  // console.log("userId", userId);
  // console.log("rolee", role);

  // if (role === "2" || role === 2) {
  //   data = data.filter((client) => client.userId == userId);
  //   console.log("datasss", data);
  // }

  const [filterText, setFilterText] = useState("");
  const [resetPaginationToggle, setResetPaginationToggle] = useState(false);

  const filteredItems = data.filter((item) =>
    item.financialYear.toLowerCase().includes(filterText.toLowerCase())
  );

  const subHeaderComponentMemo = useMemo(() => {
    const handleClear = () => {
      if (filterText) {
        setResetPaginationToggle(!resetPaginationToggle);
        setFilterText("");
      }
    };

    return (
      <FilterComponent
        onFilter={(e) => setFilterText(e.target.value)}
        onClear={handleClear}
        filterText={filterText}
      />
    );
  }, [filterText, resetPaginationToggle]);

  return (
    <>
      <div className="d-flex justify-content-between m-3">
        <h5></h5>
        <Button
          onClick={() => navigate("/financialyears/create")}
          variant="primary"
        >
          <i className="fa-solid fa-plus"></i> New financial year
        </Button>
      </div>
      <Card>
        <Card.Body>
          <DataTable
            title="Financial year List"
            columns={columns}
            data={filteredItems}
            pagination
            paginationResetDefaultPage={resetPaginationToggle}
            subHeader
            subHeaderComponent={subHeaderComponentMemo}
            persistTableHead
          />
        </Card.Body>
      </Card>
    </>
  );
};

export default Fys;
