import React, { useEffect, useMemo, useState } from 'react'
import { Button, Card } from "react-bootstrap";
import DataTable from "react-data-table-component";
import { useNavigate } from "react-router-dom";
import { useSelector } from 'react-redux';
import CVMethods from '../../../asyncMethods/CVMethods';
import FilterComponent from '../../../components/FilterComponent/Index';
import api from '../../../api';
import UserMethods from '../../../asyncMethods/UserMethods';
import NewNewsButton from '../../../components/ActionButtons/NewsButton';

const News = () => {
  const navigate = useNavigate();
  const cVMethods = CVMethods();
  const userMethods = UserMethods();
  const [datas, setDatas] = useState([]);
  const [users, setUsers] = useState([]);

    const columns = [
      {
        name: "News Id",
        selector: (row) => row.newsId,
        sortable: true,
      },
      {
        name: "News Text",
        selector: (row) => row.newsText,
        sortable: true,
      },
      {
        name: "News Title",
        selector: (row) => row.newsTitle,
        sortable: true,
      },
      {
        name: "News Image",
        selector: (row) => row.imgButton,
        sortable: true,
      },
      {
        name: "Action",
        selector: (row) => row?.actionButtons,
        center: true,
      },
    ];

  const user = JSON.parse(localStorage.getItem("user"));
  // const userId = user?.userID;
  // const role = user?.roleType;

  useEffect(() => {
   cVMethods.fetchAllFys();

  const fetchAllUsers = () => {
    userMethods.checkSessionExpiryOnDashboard();
    const jwt = localStorage.getItem("jwt");

    const headers = {
      headers: {
        Authorization: `Bearer ${jwt}`
      }
    };
    return api
      .get("/user/getallnews", headers)
      .then(res => {
        if(res.data.statusCode == 500){
          return;
        }
        else{
          setDatas(res.data.result);
          console.log("ussss", res.data.result);   
        }

      })
      .catch(err => {
        console.log(err);
      });
  };
  
    fetchAllUsers();

  }, []);


  let data = [];

  const all = useSelector(
    (state) => state
  );

  console.log("all", all);

   if (datas) {
     data = datas.map((inspection) => ({
       ...inspection,
       actionButtons: (
         <NewNewsButton
           clientDetails={inspection}
         />
       ),
       imgButton: (
        <img style={{height:"28px", width:"28px"}} src={inspection.newsImage} alt=''></img>
       ),
     }));
   }


  const [filterText, setFilterText] = useState("");
  const [resetPaginationToggle, setResetPaginationToggle] = useState(false);

  const filteredItems = data.filter((item) =>
    item.newsText.toLowerCase().includes(filterText.toLowerCase()) ||
    item.newsTitle.toLowerCase().includes(filterText.toLowerCase())
  );

  const subHeaderComponentMemo = useMemo(() => {
    const handleClear = () => {
      if (filterText) {
        setResetPaginationToggle(!resetPaginationToggle);
        setFilterText("");
      }
    };

    return (
      <div className='d-flex'>
     <FilterComponent
        onFilter={(e) => setFilterText(e.target.value)}
        onClear={handleClear}
        filterText={filterText}
      />
      </div>
    );
  }, [filterText, resetPaginationToggle, users]);

  return (
    <>
      <div className="d-flex justify-content-between m-3">
        <h5></h5>
        <Button
          onClick={() => navigate("/news/create")}
          variant="primary"
        >
          <i className="fa-solid fa-plus"></i> New News
        </Button>
      </div>
      <Card>
        <Card.Body>
          <DataTable
            title="News"
            columns={columns}
            data={filteredItems}
            pagination
            paginationResetDefaultPage={resetPaginationToggle}
            subHeader
            subHeaderComponent={subHeaderComponentMemo}
            persistTableHead
          />
        </Card.Body>
      </Card>
    </>
  );
};

export default News;


