import React, { useEffect, useMemo, useState } from "react";
import { Button, Card } from "react-bootstrap";
import DataTable from "react-data-table-component";
import { useNavigate } from "react-router-dom";
import { useSelector } from "react-redux";
import CVMethods from "../../../asyncMethods/CVMethods";
import FilterComponent from "../../../components/FilterComponent/Index";
import api from "../../../api";
import UserMethods from "../../../asyncMethods/UserMethods";
import ItrRequestButton from "../../../components/ActionButtons/ItrRequestButton";
import { toast } from "react-toastify";

const ItrRequest = () => {
  
  const navigate = useNavigate();
  const cVMethods = CVMethods();
  const userMethods = UserMethods();
  const [datas, setDatas] = useState([]);
  const [users, setUsers] = useState([]);

  const jwt = localStorage.getItem("jwt");

  const headers = {
    headers: {
      Authorization: `Bearer ${jwt}`
    }
  };

  const columns = [
    { name: 'id', selector: (row) => row.subPayId},
    { name: 'user id', selector: (row) => row.userId.id},
  
    { name: "Name ", selector: (row) => row.userId.name },
    {
      name: "Email",
      selector: (row) => row.userId.emailId
    },
    {
      name: "Mobile No. ",
      selector: (row) => row.userId.mobileNumber
    },
    {
      name: "Payment",
      selector: (row) => row.paymentAmt
    },
    {
      name: "Service",
      selector: (row) => row.serviceName
    },
    {
      name: "status ",
      selector: (row) => row.statusbtn
    },
    {
      name: "Actions",
      selector: (row) => row.actionButtons,
      center: true
    }
  ];

  // const columns = [
  //   {
  //     name: "Id",
  //     selector: (row) => row.id,
  //     sortable: true
  //   },
  //   {
  //     name: "company Name",
  //     selector: (row) => row.companyName,
  //     sortable: true
  //   },
  //   // {
  //   //   name: "financial Year",
  //   //   selector: (row) => row.financialYear,
  //   //   sortable: true,
  //   // },
  //   {
  //     name: "Service Name",
  //     selector: (row) => row?.Service
  //   },
  //   {
  //     name: "Email Id",
  //     selector: (row) => row?.Email
  //   },
  //   {
  //     name: "Mobile Number",
  //     selector: (row) => row?.Mobile
  //   },
  //   {
  //     name: "Payment",
  //     selector: (row) => row?.Payment
  //   },
  //   {
  //     name: "Staus",
  //     selector: (row) => row.statusbtn,
  //     sortable: true,
  //     center: true
  //   },

  //   {
  //     name: "Action",
  //     selector: (row) => row?.actionButtons,
  //     center: true
  //   }
  // ];

  const user = JSON.parse(localStorage.getItem("user"));
  // const userId = user?.userID;
  // const role = user?.roleType;

  useEffect(() => {
    const fetchAllUsers = () => {
      userMethods.checkSessionExpiryOnDashboard();
     
      return api
      .get("/user/getallcorpsubscritionpayment", headers)
        // .get("/user/getallcompanyitrmaster", headers)
        .then((res) => {
          setDatas(res.data.result);
          console.log("ussss", res.data.result);
        })
        .catch((err) => {
          console.log(err);
        });
    };

    fetchAllUsers();
  }, []);

  const callUpdateApi = (client) => {
    const data = {
      "serviceStatus" : true
    }
    api.put(`user/updatesubscriptionpaymentrequest?subPayId=${client.subPayId}`, data ,headers).then((response) =>{
      toast.success("Completed subscription");
      window.location.reload();
    })
    .catch((error) =>{
      toast.error("Error updating subscription")
    })
  }

  let data = [];

  const all = useSelector((state) => state);

  console.log("all", all);

  if (datas) {
    data = datas.map((inspection) => ({
      ...inspection,
      statusbtn: (
        <div
          style={{ width: "48vw", marginLeft: "1rem" }}
          className="d-flex flex-column align-items-start justify-content-start"
        >
          <p style={{ fontSize: "0.77rem", marginLeft: "2.5rem" }}>{inspection.serviceStatus == false ? "pending" : "completed"}</p>
          {/* <p style={{ fontSize: "0.77rem", marginLeft: "2.5rem" }}>Pending</p> */}
          <button
            onClick={() => callUpdateApi(inspection)}
            type="button"
            style={{
              width: "8rem",
              background: "#f58720",
              border: "none",
              color: "white",
              padding: "8px 0",
              marginBottom: "8px"
            }}
          >
            Mark as Complete
          </button>
        </div>
      ),
      actionButtons: <ItrRequestButton clientDetails={inspection} />,
      imgButton: (
        <img
          style={{ height: "28px", width: "28px" }}
          src={inspection.advImage}
          alt=""
        ></img>
      )
    }));
  }

  const [filterText, setFilterText] = useState("");
  const [resetPaginationToggle, setResetPaginationToggle] = useState(false);

  let filteredItems;
  if (data !== undefined && data !== null) {
    filteredItems = data;
    // filteredItems = data.filter((item) =>
    //   //item.advText.toLowerCase().includes(filterText.toLowerCase())
    //   item.advText.toLowerCase().includes(filterText.toLowerCase())
    // );
  }

  const subHeaderComponentMemo = useMemo(() => {
    const handleClear = () => {
      if (filterText) {
        setResetPaginationToggle(!resetPaginationToggle);
        setFilterText("");
      }
    };

    return (
      <div className="d-flex">
        <FilterComponent
          onFilter={(e) => setFilterText(e.target.value)}
          onClear={handleClear}
          filterText={filterText}
        />
      </div>
    );
  }, [filterText, resetPaginationToggle]);

  return (
    <>
      <div className="d-flex justify-content-between m-3">
        <h5></h5>
        {/* <Button
          onClick={() => navigate("/ads/create")}
          variant="primary"
        >
          <i className="fa-solid fa-plus"></i> New Ad
        </Button> */}
      </div>
      <Card>
        <Card.Body>
          <DataTable
            title="Itr Request"
            columns={columns}
            data={filteredItems}
            pagination
            paginationResetDefaultPage={resetPaginationToggle}
            subHeader
            subHeaderComponent={subHeaderComponentMemo}
            persistTableHead
          />
        </Card.Body>
      </Card>
    </>
  );
};

export default ItrRequest;
