import React from "react";
import { Button, Form } from "react-bootstrap";
import { useNavigate } from "react-router-dom";
import api from "../../api";
import { toast } from "react-toastify";
import UserMethods from "../../asyncMethods/UserMethods";

const NewNewsButton = ({ clientDetails }) => {
  const navigate = useNavigate();
  const userMethods = UserMethods();

  userMethods.checkSessionExpiryOnDashboard();
  const jwt = localStorage.getItem("jwt");

    const headers = {
      headers: {
        Authorization: `Bearer ${jwt}`
      }
    };

  const redirectToEditPage = (id) => {
    api.delete(`user/deletenews?newsId=${id}`, headers).then((response) => {
      toast.success("Deleted successfully");
    })
    .then((response) => {
      setTimeout(() => {
        window.location.reload();
       }, 1000);
    })
    .catch((error) => {
      console.log(error);
      toast.error(error.message);
    });
  };


  return (
    <>
      <div className="d-flex align-items-center">
        <Button
          variant="primary"
          size="sm"
          onClick={() => redirectToEditPage(clientDetails?.newsId)}
        >
          <i class="fa-solid fa-trash"></i>
        </Button>
      </div>
    </>
  );
};

export default NewNewsButton;
