import React, { useEffect, useMemo, useState } from "react";
import { Button, Card } from "react-bootstrap";
import DataTable from "react-data-table-component";
import { useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import UserMethods from "../../../asyncMethods/UserMethods";
import UserActionButtons from "../../../components/ActionButtons/UserActionButtons";
import FilterComponent from "../../../components/FilterComponent/Index";

const columns = [
  // {
  //   name: "User Type",
  //   selector: (row) => (row.userType == 1 ? "Individual" : "Corporate"),
  // },
  { name: "id", selector: (row) => row.id },
  {
    name: "Name",
    selector: (row) => row.name
  },
  {
    name: "Email",
    selector: (row) => row.emailId
  },
  {
    name: "Mobile",
    selector: (row) => row.mobileNumber
  },
  {
    name: "deviceId",
    selector: (row) => row.deviceId
  },
  {
    name: "IT Password",
    selector: (row) => row.incomeTaxPassword
  },
  // {
  //   name: "Role",
  //   selector: (row) => (row.roleType == 1 ? "Admin" : "User"),
  // },
  {
    name: "aadhar",
    selector: (row) => row.aadhar
  },
  {
    name: "panNumber",
    selector: (row) => row.panNumber
  },
  {
    name: "membershipNumber",
    selector: (row) => row.membershipNumber
  },
  {
    name: "cin",
    selector: (row) => row.cin
  },
  {
    name: "panVerified",
    selector: (row) => (row.panVerified === true ? "true" : "false")
  },
  {
    name: "aadharVerified",
    selector: (row) => (row.aadharVerified === true ? "true" : "false")
  },
  {
    name: "Free Plan",
    selector: (row) => row.freePlanBtn,
    // center: true
  },
  {
    name: "Basic Plan",
    selector: (row) => row.basicPlanBtn,
    // center: true
  },
  {
    name: "Premium Package",
    selector: (row) => row.PremiumBtn,
    // center: true
  },
  {
    name: "Actions",
    selector: (row) => row.actionButtons,
    center: true
  }
];

const UsersIndi = () => {
  const userMethods = UserMethods();
  const [userList, setUserList] = useState(null);

  const navigate = useNavigate();

  useEffect(() => {
    userMethods.fetchAllUsers();
  }, []);

  const statusUpdate = (userId, status) => {
    const data = {
      id: userId,
      status: status == 1 ? 0 : 1
    };
    userMethods.userStatusUpdate(data);
  };

  const all = useSelector((state) => state);
  console.log("all", all);

  const userList2 = useSelector((state) => state?.authUser?.userList);

  useEffect(() => {
    // const userLists = userList2.filter(user => user.userType == 2);
    let userLists;
    if (userList2 != null && Array.isArray(userList2)) {
      userLists = userList2.filter((user) => user.userType == 2);
    }
    setUserList(userLists);
  }, [userList2]);

  let data = [];

  if (userList != undefined) {
    data = Array.from(userList)
      .map((user) => ({
        ...user,
        // freePlanBtn: (
          
        // ),
        // basicPlanBtn: (
          
        // ),
        // PremiumBtn: (
          
        // ),
        actionButtons: (
          <UserActionButtons onStatusUpdate={statusUpdate} userDetails={user} />
        )
      }))
      .sort((b, a) => a.userId - b.userId);
  } else {
    data = [];
  }

  const [filterText, setFilterText] = useState("");
  const [resetPaginationToggle, setResetPaginationToggle] = useState(false);

  const filteredItems = data.filter(
    (item) =>
      item.name.toLowerCase().includes(filterText.toLowerCase()) ||
      item.mobileNumber
        .toString()
        .toLowerCase()
        .includes(filterText.toLowerCase())
  );

  const subHeaderComponentMemo = useMemo(() => {
    const handleClear = () => {
      if (filterText) {
        setResetPaginationToggle(!resetPaginationToggle);
        setFilterText("");
      }
    };

    return (
      <FilterComponent
        onFilter={(e) => setFilterText(e.target.value)}
        onClear={handleClear}
        filterText={filterText}
      />
    );
  }, [filterText, resetPaginationToggle]);

  return (
    <>
      <div className="d-flex justify-content-between m-3">
        <h5></h5>
        <Button
          onClick={() => navigate("/users/individual/create")}
          variant="primary"
        >
          <i className="fa-solid fa-plus"></i> New User
        </Button>
      </div>
      <Card>
        <Card.Body>
          <DataTable
            title="User List"
            columns={columns}
            data={filteredItems}
            pagination
            paginationResetDefaultPage={resetPaginationToggle}
            subHeader
            subHeaderComponent={subHeaderComponentMemo}
            persistTableHead
          />
        </Card.Body>
      </Card>
    </>
  );
};

export default UsersIndi;
