import React from 'react'
import { Button, Form } from 'react-bootstrap';
import { useNavigate } from 'react-router-dom';
import { toast } from 'react-toastify';
import UserMethods from '../../asyncMethods/UserMethods';
import api from '../../api';

const ManageAdminButton = ({userDetails,onStatusUpdate}) => {

  const navigate = useNavigate();
  const userMethods = UserMethods();

  userMethods.checkSessionExpiryOnDashboard();
  const jwt = localStorage.getItem("jwt");

    const headers = {
      headers: {
        Authorization: `Bearer ${jwt}`
      }
    };


  const redirectToEditPage = id => {
    navigate("/manage-admins/edit/"+userDetails.userId);
  }

  const redirectToDelPage = (id) => {
    api.delete(`user/deleteadminbyid?userId=${id}`, headers).then((response) => {
      toast.success("Deleted successfully");
    })
    .then((response) => {
      setTimeout(() => {
        
        window.location.reload();
       }, 1000);
    })
    .catch((error) => {
      console.log(error);
      toast.error(error.message);
    });
  };

  return (
    <>
      <div className='d-flex align-items-center'>
        <Button variant="primary" size="sm" onClick={() => redirectToEditPage(userDetails.userId)} >
          <i className="fa-solid fa-user-pen"></i>
        </Button>
        <Button
        className='ml-3'
          variant="primary"
          size="sm"
          onClick={() => redirectToDelPage(userDetails.userId)}
        >
          <i class="fa-solid fa-trash"></i>
        </Button>
        {/* <Form.Check
          inline
          className={'mx-3 text-danger'}
          type="switch"
          value = { userDetails.activeStatus === 1 ? 1 : 0}
          checked = { userDetails.activeStatus === 1 ? true : false}
          id={'toggle-button'+userDetails.id}
          onChange = {(e) => {onStatusUpdate(userDetails.id,e.target.value)}}
        /> */}
      </div>
    </>
  );
}

export default ManageAdminButton;