import React from "react";
import { Button, Form } from "react-bootstrap";
import { useNavigate } from "react-router-dom";

const FyActionButtons = ({ clientDetails, onStatusUpdate }) => {
  const navigate = useNavigate();
  const redirectToEditPage = () => {
    // navigate("/client-vehicle-master/edit/" + inspectionDetails?.inspectionId);
    navigate("/financialyears/edit/" + clientDetails?.financialYearId);
  };

  const user = JSON.parse(localStorage.getItem("user"));
  const roleType = user?.roleType;
  const userId = user?.userID;
  const client = user?.client;
  const clientId = user?.userID;
  // console.log("role", roleType);

  return (
    <>
      <div className="d-flex align-items-center">
        <Button
          variant="primary"
          size="sm"
          onClick={() => redirectToEditPage(clientDetails?.financialYearId)}
        >
          <i className="fa-solid fa-user-pen"></i>
        </Button>
        {/* <Form.Check
          inline
          className={"mx-3 text-danger"}
          type="switch"
          vvalue={clientDetails.activeStatus === true ? 1 : 0}
          checked={clientDetails.activeStatus === true}
          id={"toggle-button" + clientDetails.financialYearId}
          onChange={(e) => {
            onStatusUpdate(clientDetails.financialYearId, e.target.value);
          }}
        /> */}
      </div>
    </>
  );
};

export default FyActionButtons;
