import { useDispatch } from "react-redux";
import { toast } from "react-toastify";
import {
  FETCH_ALL_CVINSPECTION_TYPE,
  SET_EDIT_CVINSPECTION_TYPE
  //   SET_INSPECTION_DETAILS,
  //   FETCH_CLIENT_INSPECTION,
  //   FETCH_USER_INSPECTION,
  //   FETCH_LAST_INSPECTION,
} from "../redux/constants/CVTypes";
import { useNavigate } from "react-router-dom";
import api from "../api";

const CVMethods = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const jwt = localStorage.getItem("jwt");
  // const user = JSON.parse(localStorage.getItem("user"));
  // const sub = JSON.parse(localStorage.getItem("sub"));
  const headers = {
    headers: {
      Authorization: `Bearer ${jwt}`
    }
  };

  function checkSessionExpiryOnDashboard() {
    const loginTime = localStorage.getItem("loginTime");

    // Check if login time is available in local storage
    if (loginTime) {
      const currentTime = new Date().getTime();
      const timePassed = currentTime - parseInt(loginTime, 10);
      const expiryTime = 600000; // 60,000 milliseconds (60 seconds)

      // Check if the specified duration has passed
      if (timePassed > expiryTime) {
        // Clear localStorage and perform logout actions
        toast.error("Jwt expired");
        localStorage.clear();
        window.location.reload();
        navigate("/");
      }
    }
  }

  const fetchAllFys = () => {
    checkSessionExpiryOnDashboard();
    return api
      .get("/opsservice/master/getallfinancialyears", headers)
      .then(res => {
        dispatch({
          type: FETCH_ALL_CVINSPECTION_TYPE,
          payload: res.data.result
        });
      })
      .catch(err => {
        console.log(err);
      });
  };

  const storeCVInspection = data => {
    return api
      .post("/opsservice/master/createfinancialyear", data, headers)
      .then(res => {
        if (res.data.statusCode == 201) {
          toast.success("created Successfully.");
          navigate(`/financialyears`);
          return;
        } else {
          toast.error(res.data.statusDescription);
          return;
        }
      })
      .catch(err => console.log(err));
  };

  const fetchCVInspection = (financialYearId = 1) => {
    return api
      .get(
        `/opsservice/master/getallfinancialyearsbyid?financialYearId=` +
          financialYearId,
        headers
      )
      .then(res => {
        dispatch({
          type: SET_EDIT_CVINSPECTION_TYPE,
          payload: res.data.result
        });
        return res.data.result;
      })
      .catch(err => {
        console.log(err);
      });
  };

  const getVehicleByFilter = (
    vehicleManufacturer = 1,
    vehicleModel = 1,
    vehicleType = 1
  ) => {
    return api
      .get(
        `/masterservice/getvehiclebymultiplefilter?vehicleManufacturer=${vehicleManufacturer}&vehicleModel=${vehicleModel}&vehicleType=${vehicleType}`
      )
      .then(res => {
        return res.data.result;
      })
      .catch(err => {
        console.log(err);
      });
  };

  const updateCVInspection = (financialYearId, masterData) => {
    const jwt = localStorage.getItem("jwt");

    const headers2 = {
      "Content-Type": "application/json",
      Authorization: `Bearer ${jwt}`
    };

    return api
      .put(
        `/opsservice/master/updatefinancialyear/` + financialYearId,
        masterData,
        { headers2 }
      )
      .then(res => {
        if (res.data.statusCode == 200) {
          toast.success("Data updated Successful.");
          navigate("/financialyears");
          return;
        }
        toast.error(res.data.statusDescription);
      })
      .catch(err => {
        console.log(err);
        toast.error("Fetch Failed.");
      });
  };

  const cvinspectionStatusUpdate = ({ id, status }) => {
    const data = {
      activeStatus: status
    };
    const headers = {
      "Content-Type": "application/json"
    };
    return api
      .put(
        `/masterservice/enabledisableclientvehiclemaster?clientVehicleId=` + id,
        data,
        {
          headers
        }
      )
      .then(res => {
        toast.success("Status Updated Successful.");
        fetchAllFys();
      })
      .catch(err => {
        console.log(err);
      });
  };

  return {
    storeCVInspection,
    fetchCVInspection,
    fetchAllFys,
    getVehicleByFilter,
    updateCVInspection,
    cvinspectionStatusUpdate
  };
};

export default CVMethods;
