import React, { useEffect, useMemo, useState } from "react";
import { Button, Card } from "react-bootstrap";
import DataTable from "react-data-table-component";
import { useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import UserMethods from "../../../asyncMethods/UserMethods";
import UserActionButtons from "../../../components/ActionButtons/UserActionButtons";
import FilterComponent from "../../../components/FilterComponent/Index";
import NewKycButton from '../../../components/ActionButtons/NewKycStatus';

const columns = [
  // {
  //   name: "User Type",
  //   selector: (row) => (row.userType == 1 ? "Individual" : "Corporate"),
  // },
  // { name: "id",
  // selector: (row) => row.id,
  // },
  {
    name: "Name",
    selector: (row) => row.name,
  },
  {
    name: "Status",
    selector: (row) => row.dir3eKycUpdate,
  },
  {
    name: "Actions",
    selector: (row) => row.actionButtons,
    center: true,
  },
];

const Kyc = () => {
  const userMethods = UserMethods();

  const navigate = useNavigate();

  useEffect(() => {
    userMethods.fetchAllUsers();
  }, []);

  const statusUpdate = (userId, status) => {
    const data = {
      id: userId,
      status: status == 1 ? 0 : 1,
    };
    userMethods.userStatusUpdate(data);
  };

  const all = useSelector(
    (state) => state
  );
  console.log("all", all);

  let userList = useSelector((state) => state?.authUser?.userList);

let data = [];

if(userList != undefined) {
   data = Array.from(userList)
    .map((user) => ({
      ...user,
      actionButtons: (
        <NewKycButton onStatusUpdate={statusUpdate} userDetails={user} />
      ),
    }))
    .sort((b, a) => a.userId - b.userId)
    
  }
  else{
    data = [];
  }

    const [filterText, setFilterText] = useState("");
    const [resetPaginationToggle, setResetPaginationToggle] = useState(false);
  
    let filteredItems = data.filter(
      (item) =>
        item.name.toLowerCase().includes(filterText.toLowerCase()) ||
        item.mobileNumber
          .toString()
          .toLowerCase()
          .includes(filterText.toLowerCase())
    );

  const subHeaderComponentMemo = useMemo(() => {
    const handleClear = () => {
      if (filterText) {
        setResetPaginationToggle(!resetPaginationToggle);
        setFilterText("");
      }
    };

    return (
      <FilterComponent
        onFilter={(e) => setFilterText(e.target.value)}
        onClear={handleClear}
        filterText={filterText}
      />
    );
  }, [filterText, resetPaginationToggle]);

  return (
    <>
      <div className="d-flex justify-content-between m-3">
        <h5></h5>
        {/* <Button onClick={() => navigate("/users/create")} variant="primary">
          <i className="fa-solid fa-plus"></i> New User
        </Button> */}
      </div>
      <Card>
        <Card.Body>
          <DataTable
            title="Kyc"
            columns={columns}
            data={filteredItems}
            pagination
            paginationResetDefaultPage={resetPaginationToggle}
            subHeader
            subHeaderComponent={subHeaderComponentMemo}
            persistTableHead
          />
        </Card.Body>
      </Card>
    </>
  );
};

export default Kyc;



