import { useDispatch } from "react-redux";
import { useNavigate } from "react-router-dom";
import { toast } from "react-toastify";
import {
  FETCH_ALL_USERS,
  FETCH_ROLE_OPTIONS,
  FETCH_STATUS_OPTIONS,
  SET_EDIT_USER
} from "../redux/constants/AuthActionTypes";
import api from "../api";
import { userLogin } from "../redux/actions/UserActions";
import axios from "axios";
import { jwtDecode } from "jwt-decode";

const UserMethods = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const jwt = localStorage.getItem("jwt");

  const headers = {
    headers: {
      Authorization: `Bearer ${jwt}`
    }
  };

  function checkSessionExpiryOnDashboard() {
    const loginTime = localStorage.getItem("loginTime");

    // Check if login time is available in local storage
    if (loginTime) {
      const currentTime = new Date().getTime();
      const timePassed = currentTime - parseInt(loginTime, 10);
      const expiryTime = 600000; // 60,000 milliseconds (60 seconds)

      // Check if the specified duration has passed
      if (timePassed > expiryTime) {
        // Clear localStorage and perform logout actions
        toast.error("Jwt expired");
        localStorage.clear();
        window.location.reload();
        navigate("/");
      }
    }
  }

  // const headers = {
  //   "access-control-allow-origin": "*",
  //   'Content-Type': 'application/json',
  //   'Accept': '*/*'
  // }

  const login = data => {
    const payload = {
      username: data.username,
      password: data.password,
      deviceId: "12365hgghh"
      // isAdmin: data.isAdmin,
    };

    // if (/^\d{10}$/.test(data.username)) {
    //   payload.mobileNumber = data.username;
    // } else {
    //   payload.email = data.username;
    // }

    return api
      .post("/auth/token", payload)
      .then(res => {
        const ress = res.data.result;
        const result = res.data.result.token;

        console.log("resss", result);

        if (Number(res.data.statusCode) === 401) {
          toast.error(res.data.statusDescription);
          return;
        }
        if (Number(res.data.statusCode) === 100) {
          toast.error(res.data.statusDescription);
          return;
        }

        if (ress != null && ress != undefined) {
          const loginTime = new Date().getTime();
          localStorage.setItem("loginTime", loginTime);
          localStorage.setItem("jwt", result);
          const userToken = jwtDecode(result);
          console.log("jwttt", userToken);
          localStorage.setItem("sub", userToken.sub);
          // var storedSub = localStorage.getItem("sub");
          const userObject = JSON.parse(localStorage.getItem("sub"));
          // var userObject = JSON.parse(storedSub);
          console.log("userObject", userObject);

          if (userObject) {
            var userName = userObject.userName;
            var user = userObject.userType;
            console.log("userType", user);
            localStorage.setItem("user", JSON.stringify(user));
          }

          if (user == 1 || user == 0) {
            dispatch(userLogin(result));
            navigate("/dashboard");
            toast.success("Login Successful.");
          } else {
            toast.error("Not Admin");
            return;
          }
        }
      })
      .catch(() => {
        toast.error("Invalid username and password");
      });
  };

  const userLogout = () => {
    localStorage.clear();
    toast.success("Logout Successful.");
    navigate("/");
  };

  const storeUser = data => {
    return api
      .post("/user/registrationservice/registerUser", data)
      .then(res => {
        if (res.data.statusCode == 201) {
          toast.success("User created Successful.");
          navigate("/users/individual");
          return;
        }
        toast.error(res.data.result);
      })
      .catch(err => console.log(err));
  };

  const fetchAllUsers = () => {
    checkSessionExpiryOnDashboard();

    return api
      .get("/user/getalluser?page=0&size=20000&sortBy=id", headers)
      .then(res => {
        dispatch({
          type: FETCH_ALL_USERS,
          payload: res.data.result.content
        });
        // fetchUserOptions("roles");
        // fetchUserOptions("userstatus");
        //fetchUser();
      })
      .catch(err => {
        console.log(err);
      });
  };

  const fetchUser = (userId = 1) => {
    return api
      .get(`/user/findUserById/${userId}`, headers)
      .then(res => {
        dispatch({
          type: SET_EDIT_USER,
          payload: res.data.result
        });
      })
      .catch(err => {
        console.log(err);
      });
  };

  const updateUser = (userId, user) => {
    // const data = {
    //   roleType: user.roleType,
    //   activeStatus: user.activeStatus
    // };

    return api
      .put(`/user/updateUserProfile/${userId}`, user, headers)
      .then(res => {
        if (res.data.statusCode == 200) {
          toast.success("User Updated Successful.");
          navigate("/users");
          return;
        }
        alert(res.data.statusDescription);
      })
      .catch(err => {
        console.log(err);
        toast.error("Update Failed.");
      });
  };

  const userStatusUpdate = ({ id, status }) => {
    const data = {
      activeStatus: status
    };
    const headers = {
      "Content-Type": "application/json"
    };
    return api
      .put(`/registrationservice/enabledisableuser?userId=` + id, data, {
        headers
      })
      .then(res => {
        toast.success("Status Updated Successful.");
        fetchAllUsers();
      })
      .catch(err => {
        console.log(err);
      });
  };

  const fetchUserOptions = datatype => {
    const actionType =
      datatype === "roles" ? FETCH_ROLE_OPTIONS : FETCH_STATUS_OPTIONS;
    return api
      .get(
        "/masterservice/masterdatalist?datatype=" +
          datatype +
          "&parentId=0&activeStatus=1"
      )
      .then(res => {
        dispatch({
          type: actionType,
          payload: res.data.result
        });
      })
      .catch(err => {
        console.log(err);
      });
  };

  const uploadFile = (id, data) => {
    checkSessionExpiryOnDashboard();
    const jwt = localStorage.getItem("jwt");
    const headers = {
      headers: {
        Authorization: `Bearer ${jwt}`
      }
    };
    return api
      .post(`/user/uploadUserDocs/60`, data, headers)
      .then(res => {
        return res.data.result;
      })
      .catch(err => console.log(err));
  };

  return {
    login,
    userLogout,
    fetchAllUsers,
    storeUser,
    fetchUser,
    updateUser,
    fetchUserOptions,
    userStatusUpdate,
    uploadFile,
    checkSessionExpiryOnDashboard
  };
};

export default UserMethods;
